/* ------------------------------------*\
    $HELPER/TRUMP CLASSES
\*------------------------------------ */

.js-toggle,
.js-toggle-parent {
  cursor: pointer;
}

.disable-link {
  pointer-events: none;
}

.u-overlay,
.u-overlay--full {
  position: relative;

  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: linear-gradient(to bottom, rgba(black, 0.35) 0%, rgba(black, 0.35) 100%) no-repeat border-box;
    z-index: -1;
  }
}

.u-overlay--bottom {
  background: linear-gradient(to bottom, rgba(black, 0.25) 0%, rgba(black, 0.25) 100%) no-repeat border-box, linear-gradient(to bottom, rgba(black, 0) 0%, rgba(black, 0.3) 100%) no-repeat border-box;
}

/**
 * Clearfix - extends outer container with floated children.
 */
.u-clear-fix {
  zoom: 1;
}

.u-clear-fix::after,
.u-clear-fix::before {
  content: " "; // 1
  display: table; // 2
}

.u-clear-fix::after {
  clear: both;
}

.u-float--right {
  float: right;
}

/**
 * Hide elements only present and necessary for js enabled browsers.
 */
.no-js .no-js-hide {
  display: none;
}

/**
 * Positioning
 */
.u-position--relative {
  position: relative;
}

.u-position--absolute {
  position: absolute;
}

/**
 * Alignment
 */
.u-text-align--right {
  text-align: right;
}

.u-text-align--center {
  text-align: center;
}

.u-text-align--left {
  text-align: left;
}

.u-center-block {
  margin-left: auto;
  margin-right: auto;
}

.u-align--center {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
}

.u-align--right {
  margin-left: auto;
}

/**
 * Background Covered
 */
.u-background--cover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

